<template>
    <a :href="fullUrl" target="_blank">
        <i v-if="!flipped && iconName" class="fa" :class="iconName"></i><slot>
        </slot><i v-if="flipped && iconName" class="fa" :class="iconName"></i>
    </a>
</template>

<script>
export default {
    props: {
        type: { type: String, default: '' },
        flipped: { type: Boolean, default: false },
        href: String,
    },

    computed: {
        fullUrl() {
            if (this.type == 'github')
                return 'https://github.com' + this.href;

            if (this.type == 'demo')
                return 'https://mfro.me' + this.href;

            if (this.type == 'email')
                return 'mailto:' + this.href;

            return this.href;
        },

        iconName() {
            if (this.type == 'github')
                return 'fa-github';

            if (this.type == 'demo')
                return 'fa-play-circle';

            if (this.type == 'email')
                return 'fa-envelope';

            return false;
        }
    }
}
</script>

<style lang="less" scoped>
i {
    color: black;
    min-width: 1em;
    text-align: center;
    margin: 0 .2em;
}
</style>



