<template>
    <div class="project">
        <header v-text="project.name" />
        <div class="summary" v-text="project.summary" />

        <div class="description" v-if="more" v-html="project.description" />

        <div class="links">
            <a href="#" class="more" @click.prevent="more = !more">
                {{ more ? 'less' : 'more...' }}
            </a>

            <div class="spacer" />

            <uikit-link v-if="project.github" type="github" :href="project.github">Github</uikit-link>

            <uikit-link v-if="project.live" type="demo" :href="project.live">Demo</uikit-link>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        project: Object,
    },

    data() {
        return {
            more: false
        };
    },
};
</script>

<style lang="less" scoped>
.project {
    width: calc(50% - 10px);
    margin: 0 0 20px 0;
    border: 1px solid lightgray;
    border-radius: 5px;
    box-sizing: border-box;

    &:nth-child(2n + 1) {
        margin-right: 10px;
    }

    &:nth-child(2n) {
        margin-left: 10px;
    }

    header {
        background: #F5F5F5;
        padding: 5px 10px;
        border-radius: 5px 5px 0 0;
    }

    .summary {
        padding: 15px 10px;
    }

    .description {
        padding: 5px 10px;
    }

    .spacer {
        flex: 1 1;
    }

    .more {
        text-decoration: none;
    }

    .links {
        display: flex;
        padding: 5px 10px;

        a {
            flex: 0 0 25%;
        }
    }
}
</style>



