<template>
    <div class="app">
        <div class="content">
            <profile/>

            <div class="header">
                Here is a list of some of the projects I have worked on over the past several years.
                Most of the projects listed here are currently accessible on my website,
                but I occasionally take them down or forget to start them. If any of
                them seem to be down, please let me know.
            </div>

            <div class="projects">
                <project v-for="(project, i) in projects" :project="project" :key="i" />
            </div>
        </div>
    </div>
</template>

<script>
import Project from './components/Project.vue';
import Profile from './components/Profile.vue';
import projects from './projects';

export default {
    name: 'app',
    components: {
        Project,
        Profile
    },

    data() {
        return {
            msg: 'Welcome to Your Vue.js App',
            projects: projects,
        }
    }
};
</script>

<style lang="less" scoped>
.app {
    font-family: 'Work Sans', 'Arial', sans-serif;
}

.content {
    width: 50%;
    margin: 0 auto;
}

.header {
    margin: 1em 0 2em 0;
}

.projects {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
}
</style>



